<template>
  <div class="list">
    <div class="list__picker">
      <Select v-model="pickData" placeholder="请选择" class="select" @on-change="selectChange">
        <Option v-for="item in pickerList" :value="item.id" :key="item.id">{{ item.gamename }}</Option>
      </Select>
    </div>
    <div class="list__cont">
      <div v-if="list.length==0" class="no__data">暂无比赛记录~</div>
      <Scroll v-if="list.length>0" :on-reach-bottom="handleReachBottom" :distance-to-edge="[0,-20]">
        <ul>
          <li class="item" v-for="(item, index) in list">
            <div class="title">
              <div class="lt">第{{parseInt(index)+1}}场</div>
              <div class="rt">{{ item.gameDetails.isdate | dateFmt}}</div>
            </div>
            <div class="cont">
              <div class="vs vs__lt">
                <div class="desc">
                  <div class="title">
                    <h3>{{gameResult(item,'team',1)}}</h3>
                    <small>所属战队</small>
                  </div>
                  <div class="result">
                    <div class="txt" :class="{'success':gameResult(item,'result',1)==1,'fail':gameResult(item,'result',1)==0}">{{gameResult(item,'result',1)==1?'胜':'败'}}</div>
                    <p>{{gameResult(item,'rank',1)}}</p>
                  </div>
                </div>
                <div class="point point__lt">{{gameResult(item,'points',1)}}</div>
              </div>
              <div class="vs vs__rt">
                <div class="desc">
                  <div class="title">
                    <h3>{{gameResult(item,'team',2)}}</h3>
                    <small></small>
                  </div>
                  <div class="result">
                    <div class="txt" :class="{'success':gameResult(item,'result',2)==1,'fail':gameResult(item,'result',2)==0}">{{gameResult(item,'result',2)==1?'胜':'败'}}</div>
                    <p>{{gameResult(item,'rank',2)}}</p>
                  </div>
                </div>
                <div class="point point__rt">{{gameResult(item,'points',2)}}</div>
              </div>
            </div>
          </li>
        </ul>
      </Scroll>
    </div>
  </div>
</template>
<script>
import { Local, Datefmt } from 'js/utils.js';

export default {
  name: 'list',
  props: [],
  data() {
    return {
      userId: Local.get('user_id') || '',
      pickerList: [
        // {
        //   id: '0',
        //   gamename: '第十届大学生联赛第十届大学生联赛'
        // },
        // {
        //   id: '1',
        //   gamename: '第九届大学生联赛'
        // },
      ],
      pickData: '',
      list: [],
      preventRepeat: false,
      pageSize: 5,
      pageNum: 0,
    };
  },
  components: {

  },
  created() {
    let that = this;
    that.$api.GetSelectList().then(res => {
      // console.log(res.data.list)
      if (Number(res.code) == 10000) {
        that.pickerList = res.data.list
      } else {
        that.$Message.warning(res.msg);
      }
    })
  },
  mounted() {
    let that = this;
    that.initList();
  },
  computed: {

  },
  filters: {
    dateFmt(value) {
      // console.log(value.replace(/\-/g, "/"))
      return Datefmt(new Date(value), 'YYYY-MM-DD');
    },

  },
  methods: {
    selectChange() {
      let that = this;
      that.initList();
    },

    initList() {
      let that = this;
      that.getlist(true, res => {
        // console.log(res)
        that.$Message.success("加载成功");
        that.list = res.data.list;
      });
    },
    handleReachBottom() {
      let that = this;
      if (that.preventRepeat) {
        return;
      }
      that.getlist(false, res => {
        // console.log(res)
        that.list = that.list.concat(res.data.list);
        if (res.data.list.length < that.pageSize) {
          that.preventRepeat = true;
          that.$Message.info("没有更多了");
          return;
        } else {
          that.preventRepeat = false;
          that.$Message.success("加载成功");
        }
      });
    },
    /**
     * 比赛结果
     */
    gameResult(item, type, flag) {
      if (item.teamid && item.gameDetails && item.gameDetails.teamid1 && item.gameDetails.teamid2) {
        //我的战队
        let myteamname = item.teamid == item.gameDetails.teamid1 ? item.gameDetails.teamname1 : item.teamid == item.gameDetails.teamid2 ? item.gameDetails.teamname2 : '';
        //其他战队
        let oteamname = item.teamid == item.gameDetails.teamid1 ? item.gameDetails.teamname2 : item.gameDetails.teamname1;

        //我的分数
        let mypoints = item.teamid == item.gameDetails.teamid1 ? item.gameDetails.num1 : item.teamid == item.gameDetails.teamid2 ? item.gameDetails.num2 : '';
        //对手分数
        let opoints = item.teamid == item.gameDetails.teamid1 ? item.gameDetails.num2 : item.gameDetails.num1;

        //胜负
        let myresult = mypoints > opoints ? 1 : 0; //1胜，0败
        let oresult = mypoints > opoints ? 0 : 1; //1胜，0败

        //我的名次
        let myrank = item.teamid == item.gameDetails.teamid1 ? (item.gameDetails.rank1 ? item.gameDetails.rank1 : '未上榜') : item.teamid == item.gameDetails.teamid2 ? (item.gameDetails.rank2 ? item.gameDetails.rank2 : '未上榜') : '';
        //对手名次
        let orank = item.teamid == item.gameDetails.teamid1 ? (item.gameDetails.rank2 ? item.gameDetails.rank2 : '未上榜') : (item.gameDetails.rank1 ? item.gameDetails.rank1 : '未上榜');

        //
        if (type == 'team') {
          return flag == 1 ? myteamname : flag == 2 ? oteamname : '';
        } else if (type == 'result') {
          return flag == 1 ? myresult : flag == 2 ? oresult : '';
        } else if (type == 'points') {
          return flag == 1 ? mypoints : flag == 2 ? opoints : 0;
        } else if (type == 'rank') {
          return flag == 1 ? myrank : flag == 2 ? orank : '';
        }
      }
    },
    getlist(isReset, callback) {
      let that = this;
      if (isReset) that.pageNum = 0;
      that.$api.SelectGamesDetails(that.pageSize, that.pageNum, that.userId, that.pickData).then(res => {
        that.pageNum++;
        "function" === typeof callback && callback(res);
      })
    }
  },
};

</script>
<style lang="scss" scoped>
.list {
  position: relative;
  min-height: 100vh;
  background: url('~img/history_bg.png') center center / 100% 100% no-repeat;

  &__picker {
    width: 234px;
    padding: 25px 0;
    margin: 0 auto;
    // border: 1px solid #fff;

    .select {
      position: relative;
      width: 100%;
      height: 45px;

      /deep/ .ivu-select-selection {
        background: url('~img/history_bg_pulldown.png') center center / 100% 100% no-repeat;
        height: 100%;
        border: 0;
        outline: 0;
        box-shadow: none;

        &:hover {
          border: 0;
        }

        .ivu-select-placeholder,
        .ivu-select-selected-value {
          display: block;
          height: 45px;
          line-height: 45px;
          font-size: 14px;
          padding: 0 30px;
          text-align: center;
          color: #E0DCAC;
        }

        .ivu-select-placeholder {
          color: #CEC9CD;
        }

        .ivu-icon {
          font-size: 20px;
          margin-top: -10px;
          right: 10px;

          &.ivu-icon-ios-arrow-down:before {
            content: "\F33D" !important;
            color: #E0DCAC;
          }
        }
      }


      /deep/ .ivu-select-dropdown {

        background-color: #282D53;

        .ivu-select-dropdown-list {
          .ivu-select-item-focus {
            background-color: #282D53;
          }

          .ivu-select-item {
            color: #E0DCAC;
            padding: 12.5px;
            font-size: 14px !important;
          }
        }
      }
    }
  }

  &__cont {
    // border: 1px solid #fff;
    width: 100%;
    height: calc(100vh - 100px);

    .no__data {
      font-size: 14px;
      text-align: center;
      color: #E0DCAC;
    }

    /deep/ .ivu-scroll-wrapper {
      height: 100% !important;

      .ivu-scroll-container {
        height: 100% !important;

        ul {
          padding: 0 15px;

          li.item {

            &+li {
              margin-top: 20px;
            }

            >.title {
              display: flex;
              justify-content: space-between;
              align-items: center;
              margin-bottom: 5px;

              >.lt {
                display: flex;
                align-items: center;
                color: #FDF2C6;
                font-size: 12px;

                &::before {
                  content: '';
                  display: block;
                  width: 18px;
                  height: 18px;
                  margin-right: 5px;
                  background: url('~img/history_sign.png') center center / 100% 100% no-repeat;
                }
              }

              >.rt {
                color: #4F525B;
                font-size: 12px;
                font-weight: 500;
              }
            }

            .cont {
              position: relative;
              width: 100%;
              height: 126px;
              background: url('~img/history_bg_vs.png') center center / 100% 100% no-repeat;

              .vs {
                position: absolute;
                height: 90px;
                width: 42%;
                top: 50%;
                transform: translateY(-50%);

                &__lt {
                  left: 2%;
                }

                &__rt {
                  right: 2%;
                }

                .desc {
                  // border: 1px solid #fff;
                  height: 100%;
                  display: flex;
                  flex-direction: column;
                  justify-content: space-between;
                  align-items: center;

                  >.title {
                    text-align: center;

                    h3 {
                      font-size: 14px;
                      font-weight: bold;
                      color: #FDF2C6;
                      line-height: 19px;
                    }

                    small {
                      display: block;
                      height: 12.5px;
                      font-size: 11px;
                      font-weight: 500;
                      color: #FFFFFF;
                    }
                  }

                  .result {
                    text-align: center;

                    .txt {
                      font-size: 16px;
                      font-weight: bold;
                      line-height: 19px;

                      &.success {
                        color: #82BF6F;
                      }

                      &.fail {
                        color: #EF4343;
                      }
                    }

                    p {
                      font-size: 12px;
                      font-weight: 500;
                      color: #FDE2C2;
                    }
                  }
                }

                .point {
                  position: absolute;
                  top: 50%;
                  transform: translateY(-50%);
                  color: #FDF2C6;

                  &__lt {
                    right: 2%;
                  }

                  &__rt {
                    left: 2%;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

</style>



